import timezones from "@/data/timezones";

import moment from "@/plugins/moment";

export const getOffset = (timezone) => {
    timezone = timezone || moment.tz.guess();

    let _timezone = timezones.find((item) => item.value === timezone);

    if (_timezone) return _timezone.offset;

    return moment.tz.zone(timezone).parse(new Date());
}