<template>
	<div class="preview_invite position-fixed" :class="{ is_open: isOpen }">
		<div class="preview_header border-bottom">
			<div class="d-flex align-items-center">
				<div class="fs-lg">{{ $t("preview") }}</div>
				<span class="mx-2 mt-1" v-b-tooltip.hover :title="$t('previewDesc')">
					<BIcon icon="question-circle" scale="1.2" class="mr-3" style="opacity: 0.3" />
				</span>
			</div>
			<b-btn variant="outline-dark" class="font-weight-500 border d-flex align-items-center px-4 py-2" @click="close">
				<BIcon icon="box-arrow-left" scale="1" class="mr-2" style="margin-bottom: 1px" />
				{{ $t("exitPreview") }}
			</b-btn>
		</div>
		<div class="preview_body">
			<b-container :style="`max-width: ${availableDate ? '1130px' : '880px'} !important`">
				<b-card style="border-radius: 10px" body-class="pt-5 pb-2 px-3 px-lg-5">
					<b-row>
						<b-col cols="12" lg="4">
							<div md="text">
								<b-avatar src="https://placekitten.com/300/300" size="10rem"></b-avatar>
								<h4 class="mt-2 text-muted">{{ $me.username }}</h4>
								<h3 class="mt-2 mb-3">{{ invite.name }}</h3>
								<div>
									<BIcon icon="clock-history" scale="1.2" class="mr-2" />
									<span class="text-muted">{{ durationStr }}</span>
								</div>
								<div class="my-3 font-weight-500 text-success" v-if="selectedDate">
									<BIcon icon="calendar4-event" scale="1.2" class="mr-2" />
									<span>{{ selectedDateFormat }}</span>
								</div>
							</div>
						</b-col>
						<b-col cols="12" lg="8">
							<div style="min-height: 561px" class="d-flex flex-column">
								<template v-if="!selectedDate">
									<span style="font-size: 1.5rem">{{ $t("selectDateAndTime") }}</span>

									<div class="custom_calendar mt-5">
										<div class="d-flex">
											<div :style="`${times.length ? 'min-width: 70%' : 'width:100%'}`">
												<date-picker
													timezone="UTC"
													v-model="date"
													:max-date="maxDate"
													:min-date="minDate"
													:disabled-dates="{ start: null, end: null }"
													:available-dates="availableDates.map((av) => av.date)"
													is-expanded
													class="border-0"
													title-position="left"
													:locale="{ id: $i18n.locale, firstDayOfWeek: 2 }"
													@update:from-page="updateFromPage"
												></date-picker>

												<div class="mt-5">
													<time-zones ref="timezone" v-model="timezone" class="custom_timezone"></time-zones>
												</div>
											</div>
											<div class="times w-100" v-if="times.length">
												<div class="mt-2 pt-1 mb-3 text-nowrap font-weight-500 pl-3">
													{{ $moment(date).format("dddd, MMM D") }}
												</div>
												<div class="times_body custom_scroll px-3">
													<div class="btn_outline" role="button" v-for="(time, i) in times" :key="i" @click="handleSelect(time.value)">
														{{ time.text }}
													</div>
												</div>
											</div>
										</div>
									</div>
								</template>
								<template v-else>
									<span style="font-size: 1.5rem" class="mb-5">{{ $t("yourContactInfo") }}</span>

									<FormInput
										v-model="contact.fullName"
										v-vuelidate="contact.fullName"
										:label="$t('fullName')"
										:placeholder="$t('fullNamePlaceholder')"
										required
										class="mb-5"
									/>

									<FormInput
										v-model="contact.email"
										v-vuelidate="contact.email"
										:label="$t('email')"
										:placeholder="$t('emailPlaceholder')"
										required
										class="mb-5"
										type="email"
									/>

									<div v-if="contact.hasGuests">
										<label for="guestsEmail" class="fs-sm text-muted">{{ $t("guestsEmail") }}</label>
										<BFormTags
											v-model="contact.guests"
											:tag-validator="isValidEmail"
											id="guestsEmail"
											:placeholder="$t('addGuestsPlaceholder')"
											:invalid-tag-text="$t('invalidEmails')"
											:add-button-text="$t('add')"
											tag-variant="primary"
										/>
									</div>

									<span v-else>
										<b-button class="filter_btn active font-weight-500" size="sm" @click="contact.hasGuests = true">
											<BIcon icon="plus-lg" scale="0.8" class="mr-2" />
											{{ $t("addGuests") }}
										</b-button>
									</span>

									<div class="d-flex align-items-center justify-content-end mt-auto py-3">
										<b-button class="filter_btn active mr-3 font-weight-500" size="sm" @click="selectedDate = null">
											<BIcon :icon="`${!$isRtl ? 'chevron-left' : 'chevron-right'}`" scale="0.8" class="mr-2" />
											{{ $t("back") }}
										</b-button>
										<span v-b-tooltip.hover :title="$t('scheduleMeetingDisabledTooltip')">
											<b-button class="filter_btn disabled font-weight-500" size="sm" disabled>
												{{ $t("scheduleMeeting") }}
											</b-button>
										</span>
									</div>
								</template>
							</div>
						</b-col>
					</b-row>
				</b-card>
			</b-container>
		</div>
	</div>
</template>

<script>
	import DatePicker from "v-calendar/lib/components/date-picker.common";
	import TimeZones from "@/components/TimeZones";
	import FormInput from "@/components/inputs/FormInput";

	import moment from "@/plugins/moment";
	import { getOffset } from "@/helpers/functions";
	import { rrulestr } from "rrule";

	import { validationMixin } from "vuelidate";

	import { required, minLength, maxLength, email } from "vuelidate/lib/validators";

	export default {
		components: { DatePicker, TimeZones, FormInput },

		mixins: [validationMixin],

		data() {
			return {
				isOpen: false,
				date: "",
				month: moment().month(),
				year: moment().year(),
				timezone: moment.tz.guess(),
				selectedDate: "",
				contact: {
					fullName: "",
					email: "",
					guests: [],
					hasGuests: false,
				},
			};
		},

		validations: {
			contact: {
				fullName: { required, minLength: minLength(4), maxLength: maxLength(50) },
				email: { required, email },
			},
		},

		computed: {
			invite() {
				return this.$store.state.invites.one;
			},

			offset() {
				return getOffset(this.invite.timezone) - getOffset(this.timezone);
			},

			minDate() {
				return this.invite.dateRange.start.clone().add(this.offset, "m").toDate();
			},

			maxDate() {
				let end = this.invite.dateRange.end;

				if (!end) return;

				return end.clone().add(this.offset, "m").toDate();
			},

			durationStr() {
				let minutes = this.invite.duration;

				if (minutes < 60) return this.$moment.duration(minutes, "minutes").humanize();

				let hours = Math.floor(minutes / 60);

				minutes = minutes % 60;

				if (minutes === 0) return this.$moment.duration(hours, "hours").humanize();

				hours = this.$moment.duration(hours, "hours").humanize();

				minutes = this.$moment.duration(minutes, "minutes").humanize();

				let separator = this.$isRtl ? " و " : " ";

				return hours + separator + minutes;
			},

			availableDates() {
				let availability = [];

				let start = this.$moment().utc().startOf("date").month(this.month).year(this.year).startOf("month");

				let end = start.clone().endOf("month");

				for (let available of this.invite.availability) {
					let rrule = rrulestr(available.rrule);

					let dates = rrule.between(start.toDate(), end.toDate());

					for (let date of dates) {
						date = this.$moment(date).utc().add(this.offset, "m");

						let startOfDate = date.clone().startOf("date");

						let dateForIncrement = date.clone();

						let dateDuration = available.duration;

						let handledDate = availability.find((d) => d.date.isSame(startOfDate, "date"));

						if (!handledDate) {
							handledDate = { date: startOfDate, intervals: [] };
							availability.push(handledDate);
						}

						while (dateDuration > 0) {
							let minutes = dateForIncrement.hour() * 60 + dateForIncrement.minute();

							let nowInviteeOffset = this.invite.minimumNotice + getOffset(this.timezone) * -1;

							if (dateForIncrement.clone().isAfter(this.$moment().utc().add(nowInviteeOffset, "m"))) {
								if (dateForIncrement.isSame(startOfDate, "date")) {
									handledDate.intervals.push({ minutes });
								} else {
									startOfDate = dateForIncrement.clone();

									handledDate = availability.find((d) => d.date.isSame(startOfDate, "date"));

									if (handledDate) {
										handledDate.intervals.push({ minutes });
									} else {
										handledDate = { date: startOfDate.clone().startOf("date"), intervals: [{ minutes }] };
										availability.push(handledDate);
									}
								}
							}

							dateDuration -= this.invite.slotIncrement;

							dateForIncrement.add(this.invite.slotIncrement, "m");
						}
					}
				}

				availability = availability.filter((av) => av.intervals.length > 0);

				availability = availability.map((av) => {
					av.intervals.sort((a, b) => a.minutes - b.minutes);

					av.date = av.date.toDate();

					return av;
				});

				return availability;
			},

			availableDate() {
				return this.availableDates.find((av) => this.$moment(this.date).utc().startOf("date").isSame(this.$moment(av.date).utc().startOf("date"), "date"));
			},

			times() {
				if (!this.availableDate) return [];

				let { date, intervals } = this.availableDate;

				if (intervals.length == 0) return [];

				date = this.$moment(date).utc().startOf("date");

				intervals = intervals.map((interval) => {
					let d = date.clone().minute(interval.minutes);
					return {
						text: d.clone().format("h:mm a"),
						value: d
							.clone()
							.add(this.offset * -1, "m")
							.toDate()
							.toISOString(),
					};
				});

				return intervals;
			},

			selectedDateFormat() {
				let date = this.$moment(this.selectedDate).utc().add(this.offset, "m");

				date = date.format("ddd, MMM D, h:mm a");

				return date + " " + this.$moment.tz(this.timezone).format("z");
			},
		},

		watch: {
			isOpen(newValue) {
				let body = document.querySelector("body");

				if (newValue) {
					body.classList.add("preview_invite_open");
					body.style.overflow = "hidden";
				} else {
					body.classList.remove("preview_invite_open");
					body.style.overflow = "auto";
					this.close();
				}
			},

			date() {
				this.selectedDate = "";
			},

			timezone() {
				this.date = "";
			},
		},

		methods: {
			open() {
				this.isOpen = true;
			},

			close() {
				this.isOpen = false;

				this.date = "";

				this.timezone = this.$moment.tz.guess();

				this.contact.hasGuests = false;
			},

			updateFromPage(data) {
				this.month = data.month - 1;
				this.year = data.year;
			},

			handleSelect(isoDate) {
				this.selectedDate = isoDate;
			},

			isValidEmail(email) {
				let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
				return re.test(String(email).toLowerCase());
			},
		},
	};
</script>

